import React, { useImperativeHandle } from "react";
import { Link, withRouter } from "react-router-dom";
import ReactQuill from 'react-quill';
import { Quill } from "react-quill";
import 'react-quill/dist/quill.snow.css';

import { isLogged, getStorageItem, removeStorageItem, setStorageItem, shop } from "../config/config";
import Api from "../config/Api";

import Header from "../components/Header";
import Footer from "../components/Footer";
import Popup from "../components/Popup";

import "../styles/admin.css";

class AdminBlog extends React.Component {

    state = {
        title: "",
        description: "",
        type: "blog",
        visibility: "visible",
        
        category: "news",

        value: ""
    }

    constructor() {
        super();

        this.handleChange = this.handleChange.bind(this)
    }

    handleChange(value) {
        this.setState({ value: value });
    }

    componentDidMount() {
        if (!isLogged()) {
            this.props.history.push("/prihlasenie")
        }
    }

    componentDidUpdate() {
        if (!isLogged()) {
            this.props.history.push("/prihlasenie")
        }
    }

    render() {
        return(
            <div className="screen" id="admin-blog">
                <div className="content">
                    <div className="title">Administrácia blogu a noviniek</div>

                    <div className="section">Pridať príspevok</div>
                    <div className="text">
                        Pridajte príspevky do databázy blogov a noviniek TerraMia. Po vyplnení všetkých políčok bude príspevok automaticky v priebehu pár sekúnd pridaný na stránku.
                    </div>

                    <div className="details">
                        <div className="heading">Názov</div>
                        <input className="field" type="text" value={this.state.title} placeholder="Názov blogového príspevku" onChange={(event) => this.setState({ title: event.target.value })} />
                        
                        <div className="heading">Popis</div>
                        <textarea className="area" rows="10" value={this.state.description} placeholder="Popis blogového príspevku" onChange={(event) => this.setState({ description: event.target.value })}></textarea>

                        <div className="heading">Typ príspevku</div>
                        <div className="problem-list" style={{ display: "flex", flexDirection: "row", gap: 10 }}>
                            <div className="item" style={this.state.type === "blog" ? { backgroundColor: "#A161B3", color: "white", fontSize: 16, padding: "10px 20px" } : { fontSize: 16, padding: "10px 20px" }} onClick={() => this.setState({ type: "blog" })}>Blog</div>
                            <div className="item" style={this.state.type === "news" ? { backgroundColor: "#A161B3", color: "white", fontSize: 16, padding: "10px 20px" } : { fontSize: 16, padding: "10px 20px" }} onClick={() => this.setState({ type: "news" })}>Novinky</div>
                        </div>

                        {this.state.type === "blog" ? <div className="heading">Viditeľnosť</div> : null}
                        {this.state.type === "blog" ? (
                            <div className="problem-list" style={{ display: "flex", flexDirection: "row", gap: 10 }}>
                                <div className="item" style={this.state.visibility === "visible" ? { backgroundColor: "#A161B3", color: "white", fontSize: 16, padding: "10px 20px" } : { fontSize: 16, padding: "10px 20px" }} onClick={() => this.setState({ visibility: "visible" })}>Viditeľný</div>
                                <div className="item" style={this.state.visibility === "locked" ? { backgroundColor: "#A161B3", color: "white", fontSize: 16, padding: "10px 20px" } : { fontSize: 16, padding: "10px 20px" }} onClick={() => this.setState({ visibility: "locked" })}>Zamknutý</div>
                            </div>
                        ) : null}

                        {this.state.type === "news" ? <div className="heading">Kategória novinky</div> : null}
                        {this.state.type === "news" ? (
                            <div className="problem-list" style={{ display: "flex", flexDirection: "row", gap: 10 }}>
                                <div className="item" style={this.state.category === "news" ? { backgroundColor: "#A161B3", color: "white", fontSize: 16, padding: "10px 20px" } : { fontSize: 16, padding: "10px 20px" }} onClick={() => this.setState({ category: "news" })}>Novinky</div>
                                <div className="item" style={this.state.category === "events" ? { backgroundColor: "#A161B3", color: "white", fontSize: 16, padding: "10px 20px" } : { fontSize: 16, padding: "10px 20px" }} onClick={() => this.setState({ category: "events" })}>Udalosti</div>
                                <div className="item" style={this.state.category === "actions" ? { backgroundColor: "#A161B3", color: "white", fontSize: 16, padding: "10px 20px" } : { fontSize: 16, padding: "10px 20px" }} onClick={() => this.setState({ category: "actions" })}>Akcie</div>
                                <div className="item" style={this.state.category === "courses" ? { backgroundColor: "#A161B3", color: "white", fontSize: 16, padding: "10px 20px" } : { fontSize: 16, padding: "10px 20px" }} onClick={() => this.setState({ category: "courses" })}>Kurzy</div>
                                <div className="item" style={this.state.category === "lectures" ? { backgroundColor: "#A161B3", color: "white", fontSize: 16, padding: "10px 20px" } : { fontSize: 16, padding: "10px 20px" }} onClick={() => this.setState({ category: "lectures" })}>Prednášky</div>
                                <div className="item" style={this.state.category === "seminars" ? { backgroundColor: "#A161B3", color: "white", fontSize: 16, padding: "10px 20px" } : { fontSize: 16, padding: "10px 20px" }} onClick={() => this.setState({ category: "seminars" })}>Semináre</div>
                            </div>
                        ) : null}
                    </div>

                    <QuillToolbar />
                    <ReactQuill
                        theme="snow"
                        value={this.state.value}
                        onChange={this.handleChange}
                        placeholder={"Napíšte niečo..."}
                        modules={modules}
                        formats={formats}
                        className="editor"
                    />

                    <div className="button-filled">Vytvoriť príspevok</div>
                </div>
            </div>
        )
    }
}

const CustomUndo = () => (
    <svg viewBox="0 0 18 18">
        <polygon className="ql-fill ql-stroke" points="6 10 4 12 2 10 6 10" />
        <path
        className="ql-stroke"
        d="M8.09,13.91A4.6,4.6,0,0,0,9,14,5,5,0,1,0,4,9"
        />
    </svg>
);
  
// Redo button icon component for Quill editor
const CustomRedo = () => (
    <svg viewBox="0 0 18 18">
        <polygon className="ql-fill ql-stroke" points="12 10 14 12 16 10 12 10" />
        <path
        className="ql-stroke"
        d="M9.91,13.91A4.6,4.6,0,0,1,9,14a5,5,0,1,1,5-5"
        />
    </svg>
);
  
  // Undo and redo functions for Custom Toolbar
function undoChange() {
    this.quill.history.undo();
}
function redoChange() {
    this.quill.history.redo();
}
  
// Add sizes to whitelist and register them
const Size = Quill.import("formats/size");
Size.whitelist = ["extra-small", "small", "medium", "large"];
Quill.register(Size, true);

// Add fonts to whitelist and register them
const Font = Quill.import("formats/font");
Font.whitelist = [
    "arial",
    "comic-sans",
    "courier-new",
    "georgia",
    "helvetica",
    "lucida"
];
Quill.register(Font, true);
  
// Modules object for setting up the Quill editor
const modules = {
    toolbar: {
        container: "#toolbar",
        handlers: {
        undo: undoChange,
        redo: redoChange
        }
    },
    history: {
        delay: 500,
        maxStack: 100,
        userOnly: true
    }
};
  
// Formats objects for setting up the Quill editor
const formats = [
    "header",
    "font",
    "size",
    "bold",
    "italic",
    "underline",
    "align",
    "strike",
    "script",
    "blockquote",
    "background",
    "list",
    "bullet",
    "indent",
    "link",
    "image",
    "color",
    "code-block"
];
  
// Quill Toolbar component
const QuillToolbar = () => (
    <div id="toolbar" className="toolbar">
        <span className="ql-formats">
        <select className="ql-size" defaultValue="medium">
            <option value="extra-small">Size 1</option>
            <option value="small">Size 2</option>
            <option value="medium">Size 3</option>
            <option value="large">Size 4</option>
        </select>
        <select className="ql-header" defaultValue="3">
            <option value="1">Heading</option>
            <option value="2">Subheading</option>
            <option value="3">Normal</option>
        </select>
        </span>
        <span className="ql-formats">
            <button className="ql-bold" />
            <button className="ql-italic" />
            <button className="ql-underline" />
            <button className="ql-strike" />
        </span>
        <span className="ql-formats">
            <button className="ql-list" value="ordered" />
            <button className="ql-list" value="bullet" />
            <button className="ql-indent" value="-1" />
            <button className="ql-indent" value="+1" />
        </span>
        <span className="ql-formats">
            <button className="ql-script" value="super" />
            <button className="ql-script" value="sub" />
            <button className="ql-blockquote" />
            <button className="ql-direction" />
        </span>
        <span className="ql-formats">
            <select className="ql-align" />
            <select className="ql-color" />
            <select className="ql-background" />
        </span>
        <span className="ql-formats">
            <button className="ql-link" />
            <button className="ql-image" />
            <button className="ql-video" />
        </span>
        <span className="ql-formats">
            <button className="ql-formula" />
            <button className="ql-code-block" />
            <button className="ql-clean" />
        </span>
        <span className="ql-formats">
            <button className="ql-undo">
                <CustomUndo />
            </button>
            <button className="ql-redo">
                <CustomRedo />
            </button>
        </span>
    </div>
);

export default withRouter(AdminBlog);